.outContainer {
  margin: auto;
  margin-top: 1rem;
  width: 20rem;
  height: 20rem;
  background-color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 7px;
  touch-action: none;
  position: relative;
}

.textarea {
  font-family: 'GangwonEdu_OTFBoldA';
  position: absolute;
  text-align: left;
  border: 2px solid orange;
  border-radius: 2%;
  resize: none;
  display: flex;
  margin: auto;
  width: 15rem;
  height: 15rem;
  /* padding: 1rem; */
  background-attachment: local;
  background-image:
    linear-gradient(to right, white 10px, transparent 10px),
    linear-gradient(to left, white 10px, transparent 10px),
    repeating-linear-gradient(white, white 30px, #ccc 30px, #ccc 31px, white 31px);
  line-height: 31px;
  padding: 0px 20px;
}

